import React from 'react'
import { Link } from 'gatsby'
import { TARGET_CLASS } from '../../utils/visible'
import _ from 'lodash'

import './index.scss'
import Image from 'gatsby-image'

export const ThumbnailItem = ({ node, index, showAll, postsVisible }) => {
  return (
    <Link className={`thumbnail-item clickable ${index === 0 && !showAll ? 'thumbnail-item--first' : ''} ${postsVisible ? 'visible' : TARGET_CLASS}`}
          to={`/blog/${_.kebabCase(node.frontmatter.title)}/`}>
      <Image className={`thumbnail-item__cover-image ${index === 0 && !showAll ? 'thumbnail-item__cover-image--first' : ''}`}
             objectFit='cover'
             fluid={node.frontmatter.image.childImageSharp.fluid}
             alt={node.frontmatter.title}
      />
      <div className={`thumbnail-item__description-container ${index === 0 && !showAll ? 'thumbnail-item--first__description-container' : ''}`}>
        <span> {node.frontmatter.type.toUpperCase()} |
          <Link to={`/tags/${_.kebabCase(node.frontmatter.tags[0])}`}> {node.frontmatter.tags[0]}</Link>
        </span>
        <h3 className='thumbnail-item__description-container--title'>{node.frontmatter.title || node.fields.slug}</h3>
        <p>{index === 0 && !showAll ? <p dangerouslySetInnerHTML={{ __html: node.excerpt }} /> : null}</p>
      </div>
    </Link>
  )
}
