import React, { useEffect, useMemo, useState } from 'react'
import { ThumbnailContainer } from '../thumbnail-container'
import { ThumbnailItem } from '../thumbnail-item'
import { CATEGORY_TYPE } from '../../constants'

export const Contents = ({
                           posts,
                           countOfInitialPost,
                           count,
                           category,
                           showAll,
                           postsVisible = false,
                         }) => {


  const refinedPosts = useMemo(() =>
    posts
    .filter(
      ({ node }) =>
        category === CATEGORY_TYPE.ALL ||
        node.frontmatter.category === category,
    )
    .slice(0, count * countOfInitialPost),
  )

  const isBrowser = () => typeof window !== 'undefined'
  const [width, setWidth] = useState(isBrowser() && window.innerWidth)

  function handleWindowSizeChange() {
    setWidth(window.innerWidth)
  }

  useEffect(() => {
    window.addEventListener('resize', handleWindowSizeChange)
    return () => {
      window.removeEventListener('resize', handleWindowSizeChange)
    }
  }, [])

  let isMobile = (width <= 768)

  return (
    <ThumbnailContainer showAll={showAll}
                        isMobile={isMobile}>

      {!showAll && <ThumbnailItem node={refinedPosts[0].node}
                                  key={`item_${0}`}
                                  index={0}
                                  postsVisible={postsVisible} />}


      {refinedPosts.map(({ node }, index) => {
          if (!showAll) {
            if (index === 0) {
              return null
            }
          }
          return (
            <ThumbnailItem node={node}
                           key={`item_${index}`}
                           index={index}
                           showAll={showAll}
                           postsVisible={postsVisible} />
          )
        },
      )}

    </ThumbnailContainer>
  )
}
