import React from 'react'
import { Link } from 'gatsby'
import './index.scss'

export const ThumbnailContainer = React.memo(({ showAll, children, isMobile }) => (
  <div className='thumbnail-container'>
    {!showAll ? <h2 className='thumbnail-container__title'>Featured <strong>posts</strong></h2> : null}

    <div className={`thumbnail-container__content ${!showAll && 'thumbnail-container__content--home'}`}>{children}</div>

    {!showAll && (
      <div className='thumbnail-container__button-container'>
        <Link to='/blog'>
          <button className={`btn ${isMobile ? 'btn-secondary' : 'btn-primary'}`}>Discover more posts</button>
        </Link>
      </div>
    )}
  </div>
))
